

.fullBodyBG {

  display: grid;
  background-color: #002d55;
  background: linear-gradient(to right top, black, rgb(7, 0, 44)) no-repeat center center fixed;
}

#mainhero{
  background-color: #002d55;
  background: linear-gradient(to right,#000002b4 , #01000faf), url('../img/hero2.jpg') no-repeat center center fixed;
  /* background: url('../img/hero1.jpg') no-repeat center center fixed; */
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;

}



.fullscreen-bg {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: -100;
}

.fullscreen-bg__video {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

  
  
  ::-webkit-scrollbar {
    width: 12px;
    height: 10px;
  }
  ::-webkit-scrollbar-track {
    background-color: transparent;
  }
  
  
  ::-webkit-scrollbar-thumb {
    background-color: #c5c5c5;
    border-radius: 20px;
    border: 2px solid transparent;
    background-clip: content-box;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background-color: #7c7c7c;
  }