p
{
  margin:0;
  padding:0;
}

.cardBackground:hover{
    transform: scale(1.05);
 box-shadow: 0 10px 20px rgba(0,0,0,.12), 0 4px 8px rgba(0,0,0,.06);
 background: linear-gradient(to right bottom,rgba(0, 0, 0, 0.6),rgba(48, 48, 48, 0.637));

}

.cardBackground{
    background: linear-gradient(to right bottom,rgba(0, 0, 0, 0.6),rgba(70, 70, 70, 0.486));
    /* background: black; */
    backdrop-filter: blur(5rem);
    transition: .3s transform cubic-bezier(.155,1.105,.295,1.12),.3s box-shadow,.3s -webkit-transform cubic-bezier(.155,1.105,.295,1.12);
    cursor: pointer;

}


.cardBackground:hover .cardImg img{
    opacity: 1;
    -webkit-transform:scale(1.02);
    -moz-transform:scale(1.02);
    -ms-transform:scale(1.02);
    -o-transform:scale(1.02);
    transform:scale(1.02);
  }