/* btn-hover color-1,2... in button py-5 */
.btn-hover {
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin: 20px;
    height: 55px;
    text-align:center;
    border: none;
    background-size: 300% 100%;

    border-radius: 50px;
    transition: all .4s ease-in-out;
}

.btn-background-glow{
    box-shadow: 0 0 40px 0 #25aae1;

}
.btn-background-glow:hover{
    background-color: #e43603;
    border-color: #e43603;
    border-radius: 20px;
    box-shadow: 0 0 40px 2px #e43603;

}

.btn-hover:hover {
    background-position: 100% 0;
