/*  */


.btn-glitch {
    display: block;
    position: relative;
    margin: 0.5em 0;
    padding: 0.8em 2.2em;
    cursor: pointer;
    background: #fff;
    border: none;
    border-radius: 0.4em;
    text-transform: uppercase;
    font-size: 1.4em;
    font-family: "Work Sans", sans-serif;
    font-weight: 500;
    letter-spacing: 0.04em;
    mix-blend-mode: color-dodge;
    -webkit-perspective: 500px;
            perspective: 500px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}
.btn-glitch:before, .btn-glitch:after {
    --z: 0px;
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    content: '';
    width: 100%;
    height: 100%;
    opacity: 0;
    mix-blend-mode: inherit;
    border-radius: inherit;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: translate3d(calc(var(--z) * 0px), calc(var(--z) * 0px), calc(var(--z) * 0px));
            transform: translate3d(calc(var(--z) * 0px), calc(var(--z) * 0px), calc(var(--z) * 0px));
}
.btn-glitch span {
    mix-blend-mode: none;
    display: block;
}
.btn-glitch:after {
    background-color: #5d00ff;
}
.btn-glitch:before {
    background-color: #ff1731;
}
.btn-glitch:hover {
    background-color: #fff65b;
    transition: background 0.3s 0.1s;
}
.btn-glitch:hover:before {
    --z: 0.04;
    -webkit-animation: translateWobble 2.2s ease forwards;
            animation: translateWobble 2.2s ease forwards;
}
.btn-glitch:hover:after {
    --z: -0.06;
    -webkit-animation: translateWobble 2.2s ease forwards;
            animation: translateWobble 2.2s ease forwards;
}

/* The Modal (background) */
.modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  }
  
  /* Modal Content/Box */
  .modal-content {
    background-color: rgba(0, 0, 0, 0.87);
    margin:15% auto; 
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
    -webkit-animation-name: animatetop;
            animation-name: animatetop;
   -webkit-animation-duration: 0.2s;
           animation-duration: 0.2s;
   box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);

  }
  
  
  /* Add Animation */
  @-webkit-keyframes animatetop {
    from {-webkit-transform: scale(.8,.6);transform: scale(.8,.6); opacity: .2}
    to {-webkit-transform: scale(1,1);transform: scale(1,1); opacity: 1}
  }
  @keyframes animatetop {
    from {-webkit-transform: scale(.8,.6);transform: scale(.8,.6); opacity: .2}
    to {-webkit-transform: scale(1,1);transform: scale(1,1); opacity: 1}
  }


